<template>
  <div v-if="Height && finished" class="bg" :style="{height:Height}">
    <!-- <div v-if="subhead">{{subhead}}:</div> -->
    <div v-if="contentText" v-html="contentText"/>
    <div v-else class="no-data">
      <div class="thumb"></div>
      <span class="tips">暂无系统消息</span>
    </div>
  </div>
  <skeleton v-else :nums="4"></skeleton>
</template>

<script>
import { messageCenterDetail, readNews } from '@/http/service/message-center'
import Vue from 'vue';
import skeleton from '@/components/skeleton'
import { Toast } from 'vant';
Vue.use(Toast);
export default {
  data() {
    return {
      Height: '',
      messageId: '',
      relationId: '',
      subhead: '', // 标题
      contentText: '', // 内容
      finished: false
    }
  },
  components: {
    skeleton
  },
  /**
   * @description 没有登录跳转到登录页面
   */
  created() {
    if (this.$store.state.isLogin) {
    // 窗口可视高度 - 55（头部）- 140 (padding值)
      this.Height = `${window.innerHeight - 195}px`
      // 接收id，和relationId
      this.messageId = this.$route.params.id
      this.relationId = this.$route.params.relationId
      /**
     * @description 消息已读接口
     */
      readNews({ messageIds: this.messageId }).then(res => {
        if (res.data.success) {
        /**
        *  @description 获取数据
        */
          messageCenterDetail({
            messageId: this.messageId,
            relationId: this.relationId
          }).then(res => {
            if (res.data.success) {
              this.subhead = res.data.data?.subhead || ''
              this.contentText = res.data.data?.context || ''
            } else {
              Toast.fail({
                message: res.data.message,
                duration: 2000
              })
            }
            this.finished = true
          })
        } else {
          Toast.fail({ message: res.data.message, duration: 2000 })
        }
      })
    } else {
      // this.$router.push('/login')
    }
  }
}
</script>
