  font-family:PingFangSC-Regular,PingFang SC;
  font-weight:400;
  color:rgba(51,51,51,1);
  line-height:0.44rem;
}
.no-data{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 158px;
  .thumb{
    width: 2.42rem;
    height: 2.4rem;
    background: url('https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/home/platform-notice-no-data.png') center center no-repeat;
    margin-bottom: .4rem;
    background-size: cover;
  }
  .tips{
    font-size: .28rem;
    font-family: PingFangSC, PingFangSC-Regular;
    font-weight: 400;
    text-align: center;
    color: #999999;
  }
}
</style>
