import http from '../http'
import url from '../url'
// import Config from '../../config/index'

// 消息中心
export const messageCenter = (params) => {
  return http.request({
    url: url.messageCenter,
    method: 'post',
    params
  })
}
// 消息已读
export const readNews = (params) => {
  return http.request({
    url: url.readNews,
    method: 'post',
    params
  })
}
// 消息中心详情页
export const messageCenterDetail = (params) => {
  return http.request({
    url: url.messageCenterDetail,
    method: 'post',
    params
  })
}
